.conteudo-principal__ofertas {
  width: 80vw;
  max-height: 600px;
  box-shadow: inset 0px 0px 30px 5px var(--shadow);
  background-color: var(--branco);
  box-sizing: content-box;
  padding: 100px 10vw;
}

.conteudo-principal__ofertas * {
  cursor: pointer;
}

.conteudo-principal__ofertas-oferta--principal {
  min-height: 420px;
  min-width: 48%;
  float: left;
  box-shadow: 0px 0px 10px 3px var(--shadow), inset 0px 0px 2000px 100px var(--preto-bg);
  background: url("ramen-porco.d73443f2.jpg") center / cover no-repeat;
  transition: background 1s ease-in-out;
}

.conteudo-principal__ofertas-oferta--principal:hover {
  background: url("https://c.tenor.com/IP1cFAOQQWsAAAAC/anime-food.gif") center / cover no-repeat;
}

.conteudo-principal__ofertas-oferta--secundaria {
  height: 200px;
  min-width: 48%;
  float: right;
  background-color: red;
  background: var(--imagem-noodles-set-1);
  box-shadow: 0px 0px 10px 3px var(--shadow), inset 0px 0px 20px 5px var(--preto-transparente);
  background-position: 0;
  background-size: 320px;
  transition: all .5s;
}

.conteudo-principal__ofertas-oferta--secundaria.cima {
  background: var(--imagem-noodles-set-2);
  background-position: 0;
  background-size: 300px;
  margin-bottom: 20px;
}

.conteudo-principal__ofertas-oferta--secundaria:hover {
  background-size: 360px;
}

.conteudo-principal__ofertas-oferta--secundaria-texto {
  float: right;
  max-width: 40%;
  color: var(--branco-transparente);
  text-shadow: 2px 2px 3px var(--shadow);
  padding: 30px 20px;
  font-family: fantasy;
  font-size: 1.625rem;
  font-weight: 500;
}

@media screen and (max-width: 1024px) {
  .conteudo-principal__ofertas {
    height: auto;
    max-height: none;
    flex-wrap: wrap;
    display: flex;
  }

  .conteudo-principal__ofertas-oferta--principal, .conteudo-principal__ofertas-oferta--secundaria {
    width: 100%;
    float: none;
    margin: 20px 0;
  }
}

@media screen and (max-width: 768px) {
  .conteudo-principal__ofertas {
    padding: 5vw 10vw;
  }

  .conteudo-principal__ofertas-oferta--secundaria-texto {
    float: none;
    color: #eee;
    width: 100%;
    height: 100%;
    max-width: none;
    box-sizing: border-box;
    background-color: rgba(0, 0, 0, .34);
    padding: 10%;
  }
}

